$grey: #415167;
$beige: #C7A17A;
$light: #EDF0F5;
$dark: #151D28;
$background: #fcfcfc;

//Animations
@mixin appear {
    animation: appear 1.5s;
    @keyframes appear {
        from {opacity: 0;}
        to {opacity: 1;}
        }
}

//font-sizes
@mixin fz-price-basic {
    font-size: 20px;
    line-height: 24px;
}

@mixin fz-title-basic {
    font-size: 22px;
    line-height: 28px;
    /* identical to box height, or 117% */
}

@mixin fz-text-basic {
    font-size: 16px;
    line-height: 22px;
}

@mixin fz-price-small {
    font-size: 18px;
    line-height: 21px;
}

@mixin fz-title-small {
    font-size: 21px;
    line-height: 25px;
    /* identical to box height, or 117% */
}

@mixin fz-text-small {
    font-size: 14px;
    line-height: 19px;
}

//box elements
@mixin content {
    font-weight: 300;
    @include fz-text-basic;
    min-height: 5rem;
}

@mixin img {
    max-width: 150px;
    float: left;
    margin-right: 5%;
    margin-bottom: 10%;
}

@mixin details {
    text-decoration: none;
    color: $dark;
    font-weight: bold;
    @include fz-text-basic;
    cursor: pointer;
    &:active {
        text-decoration: underline;
    }
}

@mixin title {
    font-weight: bold;
    @include fz-title-basic;
     
}

@mixin price {
    color: $beige;
    font-weight: 600;
    @include fz-price-basic;
    margin-bottom: 2%;
    /* identical to box height, or 120% */
}

@mixin btn {
    font-family: Nunito, sans-serif;
    background-color: $beige;
    color: $light;
    border-radius: 19px;
    float: left;
    //width: 100px;
    font-size: 16px;
    padding: 10px;
    margin-right: 10%;
    cursor: pointer;
    border: none;
    box-shadow: 3px 3px 10px 3px rgb(185, 185, 185);
}

@mixin btn-viewCart {
    @include btn;
    background-color: $grey;
    display: block;
    text-align: center;
    text-decoration: none;
    color: $light;
    & a {
        text-decoration: none;
        color: $light;
    }
    &.hidden {
        display: none;
    }
}

@mixin item-box {
    background-color: #fff;
    border-radius: 5px;;
    box-sizing: border-box;
    box-shadow: 5px 5px 15px 5px rgb(185, 185, 185);
    padding: 2rem;
}

body {
    background-color: $background;
    max-width: 1400px;
    margin: 0 auto;
}

.errMess {
    color: red;
    font-weight: bold;
    font-size: 0.9rem;
}
